@use 'background.scss';
@use 'stylesheet.scss';
* {
  font-family: 'Digital-7 Mono';
}
.App {
  text-align: center;
}

.App-header {
  background: radial-gradient(circle, rgba(238,174,202,1) 0%, rgba(189,148,233,1) 100%);
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}
.setTimeWrapper {
  display: flex;
  flex-direction: row;
  @media  (max-width: 750px) {
    flex-direction: column;
  }
}
.wrapper {
  background: radial-gradient(circle, rgba(92,92,208,1) 0%, rgba(181,197,255,1) 100%);
  @media  (max-width: 800px) {
    padding: 2.0em;
  }
  width: 45vw;
  height: 20rem;
  border: 2rem solid rgb(172, 130, 52);
  border-radius: 5%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  z-index: 1;
}
.controller {
  display: flex;
  flex-direction: row;
  div {
    margin: 0 1rem 0 1rem;
  }
  div:hover{
    cursor: pointer;
    color: aqua;
    transform: scale(1.3);
    transition: 0.5s ease-in-out;
  }
}
.setTime {
  font-family: sans-serif;
  margin: 0 10px 0 10px;
  input {
    font-weight: 700;
    font-size: 1rem;
    background-color: #282c34;
    color: white;
    width: 40%;
    height: 20%;
  }
}
.programmerInfo {
  text-decoration: none;
  font-size: 1.5rem;
  margin-top: 1vh;
  transition: all .2s ease-in-out;
  font-style: italic;
  font-weight: 600;
}
.programmerInfo:hover{
  transform: scale(1.1);
}
.clock {
  font-weight: 700;
  margin: 1rem;
}
